.contacts {
	padding: 400px 0 0;
	position: relative;
	@include r(1700) {
		padding: 240px 0 0; }
	@include sm {
		padding: 130px 0 0; }
	&__deco {
		z-index: -1;
		position: absolute;
		top: 0;
		transform: translate(-35%, -43%);
		left: 50%;
		width: 1800px;
		@include r(1700) {
			width: 1500px;
			transform: translate(-35%, -43%); }
		@include sm {
			transform: translate(-35%, -37%);
			width: 450px; }
		img {
			width: 100%; } }
	&__info {
		max-width: 1200px;
		margin: 0 auto; }
	&__buttons {
		margin-top: 60px;
		display: flex;
		justify-content: center;
		@include sm {
			margin-top: 30px; }
		.btn + .btn {
			margin-left: 70px;
			@include md {
				margin-left: 40px; }
			@include sm {
				margin-left: 13px; } } }
	&__map {
		display: block;
		position: relative;
		margin-bottom: 400px;
		padding-top: 50%;
		@include r(1700) {
			margin-bottom: 200px; }
		@include laptop {
			margin-bottom: 100px; }
		@include sm {
			margin-bottom: 10px; }
		@include xs {
			padding-top: 80%;
			margin: 0 -20px 10px; }
		img {
			@include coverdiv;
			object-fit: cover; } } }

.c-item {
	margin: 23px 0;
	display: flex;
	align-items: center;
	@include md {
		margin: 40px 0; }
	@include sm {
		margin: 15px 0; }
	&__title {
		width: 45%;
		@include css-lock($addressFontMobile, $addressTitles, 375, 1920);
		font-family: $extraBoldFont;
		text-align: right;
		padding-right: 20px;
		@include laptop {
			width: 30%; }
		@include sm {
			width: 25%;
			padding-right: 10px; } }
	&__icon {
		max-width: 50px;
		width: 100%;
		@include css-lock(20, 50, 375, 1920);
		display: flex;
		justify-content: center;
		@include sm {
			max-width: 40px; }
		@include xs {
			max-width: 30px; }
		.icon {
			max-width: 100%;
			display: block; } }
	&__text {
		width: 50%;
		@include css-lock($addressFontMobile, $addressText, 375, 1920);
		padding-left: 20px;
		@include laptop {
			width: 70%; }
		@include sm {
			padding-left: 10px; }
		a {
			color: inherit;
			&:not(.info-link) {
				text-decoration: none; } } } }
