.subscribe {
	padding: 400px 40px 0;
	position: relative;
	@include sm {
		padding: 160px 40px 0; }
	&__deco {
		z-index: -1;
		position: absolute;
		top: 100px;
		transform: translate(-28.5%, 0);
		left: 50%;
		width: 2000px;
		@include sm {
			width: 600px;
			top: 65px;
			transform: translate(-30%, 0); }
		img {
			width: 100%; } }
	&__inner {
		max-width: 850px;
		margin: 0 auto 50px;
		@include sm {
			max-width: 400px;
			margin: 0 auto 40px;
			padding: 0 10px; } }
	.text {
		max-width: 640px;
		margin: 0 auto; }
	&__form {
		max-width: 1100px;
		margin: 0 auto;
		padding-left: 140px;
		@include laptop {
			max-width: 900px;
			padding-left: 100px; }
		@include sm {
			max-width: 400px;
			padding-left: 0; }
		@include r(375) {
			margin: 0 -20px; } }
	&__social {
		display: flex;
		justify-content: center;
		margin-top: 280px;
		@include sm {
			margin-top: 80px; } } }

.form-line {
	display: flex; }

.input {
	flex-grow: 1;
	max-width: 505px;
	margin-right: 50px;
	border: 7px solid $inputBorderColor;
	padding: 15px;
	border-radius: 25px;
	@include css-lock($btnFontSizeMobile, $btnFontSizeLg, 275, 1920);
	font-family: $boldFont;
	font-weight: bold;
	@include placeholder-color($textColor);
	@include sm {
		border-width: 3px;
		border-radius: 10px;
		padding: 5px 10px;
		margin-right: 10px;
		width: 100%;
		max-width: none;
		display: block; } }
