.text-banner {
	color: #ffffff;
	background-color: $visionBg;
	text-align: center;
	padding: 40px 20px 50px;
	border-width: 8px 0 8px 0;
	border-style: solid;
	border-color: $visionBorder;
	margin: 70px 0;
	@include sm {
		margin: 35px 0;
		border-width: 6px 0;
		padding: 20px 40px; }
	@include xs {
		padding: 20px; }
	&__title {
		font-family: $extraBoldFont;
		font-weight: bold;
		@include css-lock($titleBannerMobile, $titleBanner, 375, 1920);
		margin-bottom: 16px;
		@include sm {
			margin-bottom: 24px; } }
	&__text {
		font-family: $boldFont;
		@include css-lock($textFontSizeMobile, $textFontSize, 375, 1920);
		max-width: 1140px;
		margin: 0 auto;
		@include sm {
			line-height: 1.6; } }
	&--philosophy {
		background-color: $philosophyBg;
		border-color: $philosophyBorder; } }
