.team {
	padding-top: 500px;
	margin-top: -100px;
	position: relative;
	z-index: 1;
	overflow: hidden;
	@include r(1700) {
		padding-top: 300px;
		margin-top: 0; }
	@include sm {
		padding-top: 110px; }
	&__deco {
		z-index: -1;
		position: absolute;
		top: 0;
		transform: translate(-35%, -35%);
		left: 50%;
		width: 1900px;
		@include r(1700) {
			width: 1600px;
			transform: translate(-40%, -38%); }
		@include laptop {
			width: 1400px;
			transform: translate(-35%, -37%); }
		@include sm {
			width: 450px;
			transform: translate(-33%,-35%); }
		img {
			width: 100%; } } }

.team-section {
	margin: 115px 0;
	@include laptop {
		margin: 50px 0; }
	@include sm {
		margin: 30px 0; }
	&__title {
		@include css-lock($teamSectionTitleMobile, $teamSectionTitle, 375, 1920);
		color: #ffffff;
		background-color: $sectionBlue;
		padding: 13px 10px 21px 150px;
		border-radius: 0 50px 50px 0;
		width: 78%;
		font-family: $extraBoldFont;
		font-weight: bold;
		margin-bottom: 40px;
		@include laptop {
			padding: 10px 10px 15px 40px; }
		@include sm {
			padding: 5px 10px 8px 15px;
			margin-bottom: 23px; }
		.red & {
			background-color: $sectionRed; }
		.orange & {
			background-color: $sectionOrange; }
		.green & {
			background-color: $sectionGreen; } }
	&__items {
		display: flex;
		flex-wrap: wrap;
		//justify-content: space-around
		margin: 74px -40px 40px;
		@include laptop {
			margin: 30px -40px; }
		@include sm {
			margin: 10px -17px; } }
	&__item {
		width: 16%;
		padding: 0 35px 30px;
		@include r(1300) {
			width: 20%; }
		@include md {
			width: 33.33%; }
		@include sm {
			padding: 0 10px 30px;
			width: 16%; }
		@include r(635) {
			width: 20%; }
		@include r(500) {
			width: 33.33%; } } }

.team-item {
	display: flex;
	flex-direction: column;
	align-items: center;
	cursor: pointer;
	&__title {
		text-align: center;
		width: 205px;
		margin-top: 30px;
		@include css-lock($textFontSizeSmMobileTeam, $textFontSizeSm, 375, 1920);
		line-height: 1.4;
		@include r(1700) {
			width: 155px; }
		@include sm {
			width: 70px;
			margin-top: 9px; } }
	&__icon {
		.icon {
			font-size: 125px;
			display: block;
			@include r(1700) {
				font-size: 56px; }
			@include sm {
				font-size: 37px; } }
		&--icon-big {
			.icon {
				@include sm {
					font-size: 46px!important; } } }
		&--icon-small {
			.icon {
				@include sm {
					font-size: 28px!important; } } } } }

.round-item {
	border-radius: 50%;
	height: 205px;
	width: 205px;
	background-color: $sectionBlue;
	display: flex;
	align-items: center;
	justify-content: center;
	@include r(1700) {
		height: 110px;
		width: 110px; }
	@include sm {
		height: 65px;
		width: 65px; }
	.icon {
		font-size: 110px;
		display: block;
		@include r(1700) {
			font-size: 56px; }
		@include sm {
			font-size: 37px; } }
	.red & {
		background-color: $sectionRed; }
	.orange & {
		background-color: $sectionOrange; }
	.green & {
		background-color: $sectionGreen; }
	.rose & {
		background-color: $sectionrose; } }

.persons {
	display: flex;
	margin: 0 -25px;
	flex-wrap: wrap;
	@include sm {
		margin: 0; }
	&__item {
		display: flex;
		padding: 25px;
		width: 33%;
		&:not(:last-child) {
			@include sm {
				margin-bottom: 30px; } }
		@include md {
			width: 50%; }
		@include sm {
			width: auto;
			padding: 0; } } }
